import {Component, EventEmitter, Input, OnInit, Output, ViewChild} from '@angular/core';
import {ChatService} from "../../utils/chat.service";
import {FormArray, FormBuilder, FormGroup, Validators} from "@angular/forms";
import {ManagerService} from "../../utils/manager.service";
import {Place, place_default} from "../../utils/messages";
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {QuestionControlService} from "../../dynamic-form/question-control.service";
import * as extra from "../../../environments/extra_place_info";

@Component({
  selector: 'cabinet-place-editor',
  templateUrl: './place-editor.component.html',
  styleUrls: ['./place-editor.component.scss'],
  providers: []
})
export class PlaceEditorComponent implements OnInit {
  @Output() dismiss = new EventEmitter()
  @Output() close = new EventEmitter()

  @Input() place: null | Place;

  @ViewChild('qr_code') qr_code;

  extra_place_info = extra.extra_place_info();

  form: FormGroup;
  active = 1;
  table;
  id: number;
  name: string;

  constructor(
    private fb: FormBuilder,
    private manager: ManagerService,
    public chat: ChatService,
    private modalService: NgbModal,
    private qcs: QuestionControlService
  ) {
  }

  get extra (): FormGroup {
    return this.form.controls.extra as FormGroup;
  }

  ngOnInit(): void {
    this.form = this.fb.group({
      name: [Validators.required],
      active: [Validators.required],
      text: [],
      address: [],
      tables: this.fb.array([]),
      managers: this.fb.array([]),
      extra: this.qcs.toFormGroup(this.extra_place_info),
      chat: this.fb.group({
        first: [place_default.first],
        comment: [place_default.comment],
        low:[place_default.low],
        high:[place_default.high],
      }),
      branding: this.fb.group({
        logo:[],
      }),
    });

    this.managers.push(
      this.fb.group({
        user: [Validators.required],
        active: [],
      })
    );

    if(this.place && this.place.tables)
      this.place.tables.forEach(t => this.addTable());

    if (this.place) {
      this.form.patchValue(this.place);
    } else {
      this.form.patchValue({
        name: 'Новое место',
        active: 'on',
        text: null,
        'address': null,
        'tables': [],
        managers: [{user: this.manager._id, active: 'on'}]
      });
    }
  }

  get tables(): FormArray {
    return this.form.get('tables') as FormArray;
  }

  get managers(): FormArray {
    return this.form.get('managers') as FormArray;
  }

  save() {
      this.place = {...this.place == null ? {} : this.place, ...this.form.value};
      this.chat.updatePlace(this.place);
      this.close.emit();
  }

  addTable() {
    const no = this.tables.length+1;
    this.tables.push(this.fb.group({
      table: [no, Validators.required],
      name: [`Столик № ${no}`],
      active: [true]
    }));
    this.form.markAllAsTouched();
  }

  deleteTable(table_idx: number) {
    this.tables.removeAt(table_idx);
  }

  showQRcode(table) {
    this.table = table.value;
    this.modalService.open(this.qr_code, {ariaLabelledBy: 'modal-basic-title'});
  }
}
