import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';

import {ChatModule} from "../chat/chat.module";

import {CabinetRoutingModule} from './cabinet-routing.module';
import {CabinetComponent} from './cabinet.component';
import {MainMenuComponent} from './main-menu/main-menu.component';
import {ContentHeaderComponent} from './content-header/content-header.component';
import {PlacesListComponent} from './places-list/places-list.component';
import {MainPageComponent} from './main-page/main-page.component';
import {IconsModule} from "../icons/icons.module";
import {ManagerChatComponent} from "./manager-chat/manager-chat.component";
import {PERFECT_SCROLLBAR_CONFIG, PerfectScrollbarConfigInterface, PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {NgbModule} from "@ng-bootstrap/ng-bootstrap";
import {ManagerProfileComponent} from './manager-profile/manager-profile.component';

import {HttpClientModule} from "@angular/common/http";
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
import {UtilsModule} from "../utils/utils.module";

const DEFAULT_PERFECT_SCROLLBAR_CONFIG: PerfectScrollbarConfigInterface = {
  suppressScrollX: true
};
import {JoditAngularModule} from 'jodit-angular';
import {QRCodeModule} from 'angularx-qrcode';
import {PlaceEditorComponent} from './place-editor/place-editor.component';
import {NgxMaskModule} from "ngx-mask";
import {DynamicFormModule} from '../dynamic-form/dynamic-form.module';
import { GraphModule } from '../graph/graph.module';
import { QRCodesComponent } from './qrcodes/qrcodes.component';
import {Ng2TelInputModule} from 'ng2-tel-input';


@NgModule({
  declarations: [
    CabinetComponent,
    MainMenuComponent,
    ContentHeaderComponent,
    PlacesListComponent,
    MainPageComponent,
    ManagerChatComponent,
    ManagerProfileComponent,
    PlaceEditorComponent,
    QRCodesComponent,
  ],
  imports: [
    CommonModule,
    ChatModule,
    FormsModule,
    ReactiveFormsModule,
    CabinetRoutingModule,
    IconsModule,
    PerfectScrollbarModule,
    HttpClientModule,
    UtilsModule,
    NgbModule,
    JoditAngularModule,
    QRCodeModule,
    DynamicFormModule,
    NgxMaskModule,
    GraphModule,
    Ng2TelInputModule,
    // NgSelectModule
  ],
  providers: [
    {
      provide: PERFECT_SCROLLBAR_CONFIG,
      useValue: DEFAULT_PERFECT_SCROLLBAR_CONFIG
    }
  ]
})
export class CabinetModule {
}
