import {Component, EventEmitter, OnDestroy, OnInit, Output} from '@angular/core';
import {ChatService} from "../../utils/chat.service";
import {ManagerService} from "../../utils/manager.service";
import {Subscription} from "rxjs";

@Component({
  selector: 'cabinet-main-menu',
  templateUrl: './main-menu.component.html',
  styleUrls: ['./main-menu.component.scss'],
  host: {'class': 'aside aside-fixed cabinet-main-menu'}
})
export class MainMenuComponent implements OnInit, OnDestroy {
  @Output() showMenu = new EventEmitter<boolean>();

  $router: Subscription;

  minimize = false;
  showProfileMenu = false;
  new_messages = 0;

  constructor(
    public chat: ChatService,
    public user: ManagerService) {
  }

  ngOnInit(): void {
    this.chat.new_message.subscribe({
      next: (m) => this.new_messages = m
    })
  }

  ngOnDestroy() {
    if (this.$router)
      this.$router.unsubscribe();
  }

  mainMenuToggle(state?:boolean){
    if (state === true)
      document.body.classList.add("mainMenuToggled");
    else if (state===false)
      document.body.classList.remove("mainMenuToggled");
    else
      document.body.classList.toggle("mainMenuToggled");
  }
}
