import {TextboxQuestion} from "../app/dynamic-form/question-textbox";
import {DropdownQuestion} from "../app/dynamic-form/question-dropdown";

export function extra_place_info() {
  return   [
    new TextboxQuestion({"key": "inn", "label": "ИНН", "value": "_____", "required": true, "order": 1, pattern: "[0-9]{10}|[0-9]{12}"}),
    new TextboxQuestion({"key": "bik", "label": "БИК", "value": "_____", "required": true, "order": 2, pattern: "[0-9]{9}"}),
    new TextboxQuestion({"key": "place_name", "label": "Название организации", "value": "_____", "required": true, "order": 3}),
    new DropdownQuestion({"key": "self", "label": "Организационно-правовая форма", "value": "ooo",
      "options": [
        {"key": "ooo", "value": "ООО"},
        {"key": "ip", "value": "ИП"},
        {"key": "pao", "value": "ПАО"},
        {"key": "oao", "value": "ОАО"},
        {"key": "ano", "value": "АНО"}
      ],
      "required": true, "order": 3
    })
  ]
}
