import {Component, Input, OnChanges, OnInit} from '@angular/core';
import * as moment from 'moment';

@Component({
  selector: 'app-value-graph',
  templateUrl: './value-graph.component.html',
  styleUrls: ['./value-graph.component.scss']
})
export class ValueGraphComponent implements OnChanges {

  @Input() title = '';
  @Input() interval = "за месяц";
  @Input() series: { value: number, name: string }[] = [];
  @Input() suffix = '';

  value: number;
  delta: number;
  interval_string;

  colorScheme = {
    domain: ['#797979']
  };

  multi: any[] = [];

  ngOnChanges() {
    this.multi = [{
      name: this.title,
      series: this.series,
    }];
    this.value = this.series[this.series.length - 1].value;
    this.delta = this.series[this.series.length - 2].value - this.value;

    const last = moment(this.series[this.series.length - 1].name);
    const prev = moment(this.series[0].name);

    const duration = last.from(prev);
    this.interval_string = duration.toString();
  }


}
