import {Component, OnInit, ViewChild} from '@angular/core';
import {ManagerService} from "../../utils/manager.service";
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {ChatService} from "../../utils/chat.service";
import {Router} from "@angular/router";
import {NotifyService} from "../../utils/notify.service";
import {PasswordValidation} from "../../utils/password-validation";
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";

import * as extra from "../../../environments/extra_manager_info";
import {PhoneService} from "../../common/phone.service";

@Component({
  selector: 'app-manager-profile',
  templateUrl: './manager-profile.component.html',
  styleUrls: ['./manager-profile.component.scss']
})
export class ManagerProfileComponent implements OnInit {
  profile: FormGroup;
  password: FormGroup;
  autologin: FormGroup;

  avatar: any;
  initial_avatar: any;
  password_change_error: any;

  extra_manager_info = extra.extra_manager_info();

  @ViewChild('change_password_dlg') change_password_dlg;
  @ViewChild('autologin_confirm_dlg') autologin_confirm_dlg;

  constructor(
    private user: ManagerService,
    private chat: ChatService,
    private router: Router,
    public notify: NotifyService,
    private fb: FormBuilder,
    private modalService: NgbModal,
    public phone: PhoneService,
  ) {
  }

  get extra(): FormGroup {
    return this.profile.controls.extra as FormGroup;
  }


  setNumber(phone) {
    console.log(`set number ${phone}`);
    this.profile.patchValue({phone: phone});
  }

  ngOnInit(): void {
    this.profile = this.user.form();
    this.profile.addControl("notify", new FormControl());

    this.password = this.fb.group({
      old_password: [null, Validators.required],
      password: [null, Validators.required],
      confirm: [null, Validators.required],
    }, {validator: PasswordValidation.MatchPassword})

    this.autologin =
      this.fb.group({
        password: [null, Validators.required]
      });

    this.chat.waitHello().then(() => {
      this.notify.check().then(sub => {
        this.profile.patchValue(this.user);
        this.profile.patchValue({notify: this.notify.notify});
        this.profile.patchValue({autologin: this.user.autologin});
        if (!this.notify.enabled)
          this.profile.controls.notify.disable();
      });

      this.user.update().then(() => {
        this.avatar = this.chat.avatar(this.user.avatar);
        this.initial_avatar = this.avatar;
      })
    })
  }

  save() {
    let profile = this.profile.value;
    if (this.avatar != this.initial_avatar)
      profile.avatar = [{img: this.avatar, active: 'on'}];

    this.user.save(this.profile.value).then(() => {
      this.router.navigate(['/cabinet/main']);
      this.notify.updateSubscription(profile.notify);
    });
  }

  autologinConfirm($event) {
    if (this.profile.value.autologin) {
      setTimeout(() => {
        $event.srcElement.blur();
        $event.preventDefault();
        this.modalService.open(this.autologin_confirm_dlg, {ariaLabelledBy: 'modal-basic-title'}).result.then(
          (result) => {
            this.user.enable_autologin(this.autologin.value.password).catch(() => this.profile.patchValue({autologin: false}));
            this.autologin.reset();
          }, (reason) => {
            this.profile.patchValue({autologin: false});
          });
      }, 10);
    }
  }

  change_password() {
    this.password_change_error = false;
    this.modalService.open(this.change_password_dlg, {ariaLabelledBy: 'modal-basic-title'});
  }

  change_pass(modal, passwords) {
    this.user.change_password(passwords.password, passwords.old_password)
      .then(
        () => modal.close(),
        () => {
          this.password_change_error = true;
        })
    this.password.reset();
  }
}

