import {Component, OnDestroy, OnInit} from '@angular/core';
import {ChatService} from "../../utils/chat.service";
import {Place, UnreadMsgs} from "../../utils/messages";
import {Subscription} from "rxjs";

@Component({
  selector: 'app-qrcodes',
  templateUrl: './qrcodes.component.html',
  styleUrls: ['./qrcodes.component.scss']
})
export class QRCodesComponent implements OnInit, OnDestroy {
  $changes: Subscription;


  places: (Place & UnreadMsgs)[];

  constructor(public chat: ChatService) {
  }

  ngOnInit(): void {
    this.places = this.chat.places;
    this.$changes = this.chat.changes.subscribe(()=>this.places = this.chat.places);
  }

  ngOnDestroy() {
    this.$changes || this.$changes.unsubscribe();
  }
}
