import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';

import {CabinetComponent} from './cabinet.component';
import {MainPageComponent} from "./main-page/main-page.component";
import {PlacesListComponent} from "./places-list/places-list.component";
import {ManagerChatComponent} from "./manager-chat/manager-chat.component";
import {ManagerProfileComponent} from "./manager-profile/manager-profile.component";
import {LoginActivate} from "../common/signin/signin.component";
import {QRCodesComponent} from "./qrcodes/qrcodes.component";

const routes: Routes = [
  {
    path: '', component: CabinetComponent, canActivateChild: [LoginActivate], children: [
      {path: 'places',  component: PlacesListComponent},
      {path: 'chats',   component: ManagerChatComponent},
      {path: 'chats/chat',   component: ManagerChatComponent},
      {path: 'main', component: MainPageComponent},
      {path: 'profile', component: ManagerProfileComponent},
      {path: 'codes', component: QRCodesComponent},
      {path: '*', redirectTo: 'main', pathMatch: 'full'}
    ],
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CabinetRoutingModule { }
