import {Component, HostListener, OnInit, ViewChild} from '@angular/core';
import {ChatService} from "../../utils/chat.service";
import {NgbModal} from "@ng-bootstrap/ng-bootstrap";
import {Place, UnreadMsgs} from "../../utils/messages";

@Component({
  selector: 'cabinet-places-list',
  templateUrl: './places-list.component.html',
  styleUrls: ['./places-list.component.scss']
})
export class PlacesListComponent implements OnInit {
  page: number;
  @ViewChild('creator') creator;
  @ViewChild('qr_code') qr_code;
  @ViewChild('editor') editor;
  @ViewChild('deleteDlg') deleteDlg;
  @ViewChild('qr_all_codes') qr_all_codes;

  dropdown: string;
  place: Place & UnreadMsgs;
  places: (Place & UnreadMsgs)[];

  constructor(public chat: ChatService, private modalService: NgbModal) {
  }

  ngOnInit(): void {
    //todo: Подключить роутер, получить из пути page, выводить не все места, а только те, которые соответствуют page
  }

  createPlace() {
    this.modalService.open(this.creator, {ariaLabelledBy: 'modal-basic-title'}).result.then(() => {
    }, () => {
    });
  }

  toggleActive(place: Place & UnreadMsgs, set?: boolean) {
    if (set === undefined)
      set = !(place.active === 'on');

    place.active = set ? 'on' : 'off';
    this.chat.updatePlace(place);
    //@TODO: Обновить данные об активносте на сервере
    console.log(place.active);
  }

  showQRcode(place: Place & UnreadMsgs) {
    this.place = place;
    this.modalService.open(this.qr_code, {ariaLabelledBy: 'modal-basic-title'});
  }

  showAllQRcode() {
    this.places = this.chat.places;
    let res = this.modalService.open(this.qr_all_codes, {ariaLabelledBy: 'modal-basic-title'});
  }

  showEditor(place?: Place & UnreadMsgs) {
    this.place = place;
    this.modalService.open(this.editor, {ariaLabelledBy: 'modal-basic-title'});
  }

  delete(place: Place & UnreadMsgs) {
    this.place = place;
    console.log(`attempt to delete ${place._id}`)
    this.modalService.open(this.deleteDlg, {ariaLabelledBy: 'modal-basic-title'})
      .result.then(
      async () => this.chat.deletePlace(place),
      () => console.log("Cancel clicked")
    );
  }

  toggle($event, place: Place & UnreadMsgs) {
    this.dropdown = this.dropdown == place._id ? null : place._id;
    $event.stopPropagation();
  }

  @HostListener('window:click', ['$event'])
  click(event) {
    this.dropdown = null;
  }

}
