import { Component, OnInit } from '@angular/core';
import {ManagerService} from "../utils/manager.service";
import {ChatService} from "../utils/chat.service";
import {User} from "../utils/messages";

@Component({
  selector: 'app-cabinet',
  templateUrl: './cabinet.component.html',
  styleUrls: ['./cabinet.component.scss']
})
export class CabinetComponent implements OnInit {
  showMenu = false;
  constructor(
    private manager: ManagerService,
    private chat: ChatService,
  ) { }

  ngOnInit(): void {
    this.manager.update().then((user: User) => this.chat.queryManager(user._id));
  }
  mainMenuMimimize(state?: boolean){
    // document.body.classList.toggle("mainMenuToggled");
    if (state === false)
      document.body.classList.remove("mainMenuToggled");
    else if (state === true)
      document.body.classList.add("mainMenuToggled");
    else
      document.body.classList.toggle("mainMenuToggled");
  }
}
